@import 'common';

body {
  margin: 0;
}

main {
  display: flex;
  position: relative;
  top: 70px;
  width: 100%;
  height: calc(100vh - 70px);
  overflow-x: hidden;
}

* {
  font-family: $j-sofia;
  cursor: $j-default, auto;
}

//////////////////////// header ////////////////////////

header {
  display: flex;
  position: fixed;
  align-items: center;
  width: 100%;
  height: 70px;
  padding: 8px;
  background: #fff;
  box-shadow: 0 4px 28px 0 #00000040;
  z-index: 1000;
  box-sizing: border-box;

  a {
    display: flex;

    img {
      width: 50px;
      cursor: $j-pointer, pointer;
      z-index: 1001;
    }
  }

  nav {
    position: fixed;
    left: 0;
    width: 100%;

    ul {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: unset;

      li {
        margin: 0 4%;
        font-size: 25px;
        font-style: normal;
        font-weight: 400;
        list-style-type: none;

        a {
          transition: text-decoration-color .5s, letter-spacing .5s, scale .5s;
          transition-timing-function: ease;
          color: inherit;
          letter-spacing: 0;
          text-decoration: underline;
          text-underline-offset: 8px;
          text-decoration-color: rgba(0, 0, 0, 0);
          scale: 1;

          @media (hover: hover) {
            &:hover {
              cursor: $j-pointer, pointer;
              scale: 1.15;
            }
          }

          @media (hover: hover) {
            &.active {
              text-decoration-color: #000;
            }
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 600px) {
  header nav ul {
    margin-left: 45px;

    li {
      margin: 0 4%;
      font-size: 22px;

      @media (hover: hover) {
        a:hover {
          font-size: 22px;
        }
      }
    }
  }
}
